.portfolio {
  padding-bottom: 10rem;

  .portfolio__container {
    position: relative;
    padding-top: 3rem;
  }

  .portfolio-title {
    max-width: 705px;
    margin-bottom: 60px;
  }

  .portfolio-card__title {
    margin-bottom: 4px;
  }

  .portfolio-card__descr {
    margin-bottom: 24px;
  }

  .portfolio-slider__btn {
    position: absolute;
    top: 120px;
    right: 1px;

    display: flex;
    // column-gap: 12px;

    &:hover {
      .portfolio-btn__icon {
        fill: var(--color-white);
      }

    }
  }

  .portfolio__more {
    display: flex;
    column-gap: 40px;
    align-items: center;
    justify-content: center;
  }

  .portfolio-btn__icon {
    width: 24px;
    height: 24px;
    fill: var(--color-grey-800);
  }

  &--interior {
    position: relative;
    padding-top: 15rem;

    .portfolio-slider__btn {
      top: 50px;
    }
  }

  &--work{
    padding-top: 18.5rem;

    .portfolio-list{
      margin-bottom: 63px;
    }

    .portfolio__more{
      column-gap: 16px;
    }

    .portfolio--work__more{
      width: 24px;
      height: 24px;
      fill: var(--color-grey-800);
    }
  }

  &--cottages{
    padding-top: 9rem;
    padding-bottom: 12rem;
  }

  .portfolio--cottages__btn{
    top: 56px;
  }
}
